import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { Row, Col } from 'antd';
import { InfoCard } from 'components/common';
import routeConstant from 'routes/route/routeConstant';

import {
  getQuickLinkEnrollmentGetEnrollSolutionNo,
  getQuickLinkEnrollmentGetCandidateNo,
  getQuickLinkEnrollmentGetCandidateEnrolledNo,
} from 'services/quicklink';

const COLORS = [
  '#0088FE',
  '#00C49F',
  '#FFBB28',
  '#FF8042',
  '#0088fed9',
  '#00c49fd9',
  '#ffbb28d9',
  '#ff8042d9',
];

const data = [
  {
    title: 'Phương án tuyển sinh',
    service: getQuickLinkEnrollmentGetEnrollSolutionNo,
    url: routeConstant.TUYEN_SINH_NHAP_HOC__PHUONG_AN_TUYEN_SINH,
  },
  {
    title: 'Ứng viên',
    service: getQuickLinkEnrollmentGetCandidateNo,
    url: routeConstant.TUYEN_SINH_NHAP_HOC__UNG_VIEN,
  },
  {
    title: 'Ứng viên đã nhập học',
    service: getQuickLinkEnrollmentGetCandidateEnrolledNo,
    url: routeConstant.TUYEN_SINH_NHAP_HOC__NHAN_GIAY_TO_NHAP_HOC,
  },
];

class DashboardEnroll extends Component {
  state = {};
  render() {
    return (
      <Row gutter={24}>
        {data.map((item, index) => (
          <Col span={6} key={item.title}>
            <InfoCard
              backgroundColor={COLORS[index % 8]}
              title={item.title}
              service={item.service}
              icon={item.icon}
              url={item.url}
            />
          </Col>
        ))}
      </Row>
    );
  }
}

export default props => (
  <Page {...props}>
    <DashboardEnroll {...props} />
  </Page>
);



